import React, {useState, useRef, useEffect} from 'react'
import he from 'he';
import AddTimeDetail from '../Component/AddTimeDetail'
import Warning from '../UtilComponent/Warning.js';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faPlus, faImage } from '@fortawesome/free-solid-svg-icons'
import { faTrashCan } from '@fortawesome/free-regular-svg-icons'
import { getFetch, getArrBySeq } from "../CommonFunction/commonFunction.js" 
import {  notShowAddEventBlock  } from '../features/handleBoolean/toggleBlock.js';
import {  fetchMainPage  } from '../features/handleBoolean/toggleFetchData.js';
import {  addNewTimeDetail , removeTimeDetail  } from '../features/handleInput/inputObj.js';
import {  useDispatch, useSelector } from 'react-redux';


import "../Style/AddEvent.scss"


export default function AddEventBlock() {
    const tagRef = new useRef("");
    const [previewUrl, setPreviewUrl] = useState(null);
    const debounceTimer = useRef(null);
    const [saving, setSaving] = useState(false);
    const [formData, setFormData] = useState({name: '',tags: '',img: '',event:[]});
    const [tags, setTags] = useState([]);
    const [steps, setSteps] = useState(1);
    const [saveWarning, setSaveWarning] = useState("");
    const timeDetail = useSelector((state) => state.inputObj.timeDetail);

    const dispatch = useDispatch();

    const removeNewTimeDetail = ()=>{
        if(timeDetail.length === 1) return;
        dispatch(removeTimeDetail());
    }

    const handleInputChange = (e)=>{
        const { name, value } = e.target;
        setFormData({
          ...formData,
          [name]: value,
        });    
        let tagHint = "";
        let ifFetchTagHing = false;
        for(let i = value.length-1; i >= 0; i--){
            let char = value[i];
            if(char !== "#" && char !== " "){
                tagHint = char + tagHint;
            }
            else if(char === " "){
                ifFetchTagHing = false;
                break;
            }else if(char === "#"){
                ifFetchTagHing = true;
                break;
            }
        }
        if(tagHint && ifFetchTagHing){
            let ifFullChar = true;
            if (/[\u4E00-\u9FA5]/.test(tagHint[tagHint.length-1])) {
                ifFullChar = true;
            } else if (/[\u3100-\u312F\u31A0-\u31BF\u4E00-\u9FA5]/.test(tagHint[tagHint.length-1])) {
                // 注音
                ifFullChar = false;
            }
            if(ifFullChar){
                clearTimeout(debounceTimer.current);
                debounceTimer.current = setTimeout(() => {
                    getFetch("https://ap9.ragic.com/js1031222/tour/4?api&v=3&subtables=0&limit=6&offset=0&where=1000143,regex,^"+tagHint).then(result=>{
                        setTags(getArrBySeq(result));
                })
                }, 500); 
            }
        }else{
            setTags([]);
        }
    }
    const removeAddEvent = () =>{
        document.querySelector("body").style.overflow = "auto";
        dispatch(notShowAddEventBlock());
    } 

    const uploadImg = (e)=>{
       setSteps(2);
       setTimeout(() => {
        let file = e.target.files[0];
        var reader = new FileReader();
        reader.onload = function(e) {
         setPreviewUrl(e.target.result)
       };
       reader.readAsDataURL(file);
       }, 500);
    }
  const saveAddEvent = ()=>{
    let fileInput2 = document.querySelector("#fileInput");
    //empty check
    if(!formData["name"]){
        setSaveWarning("請輸入活動名稱");
        return;
    }
    let emptySubName = timeDetail.find(item=>item["name"].length === 0);
    if(emptySubName){
        setSaveWarning("請輸入地點");
        return;
    }

    setSaving(true);

    setTimeout(() => {
        let img = document.querySelector("#insertImg");
        var canvas = document.createElement('canvas');
        canvas.width = 450;
        canvas.height = 300;
        var ctx = canvas.getContext('2d');
        ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
        var resizedDataUrl = canvas.toDataURL('image/webp');

        const submitFormData = new FormData();
        submitFormData.append("1000130", he.escape(formData["name"]));
        submitFormData.append("1000132", he.escape(formData["tags"]));
        submitFormData.append("1000133", resizedDataUrl);
        submitFormData.append("1000140", 0);
        submitFormData.append("1000141", 0);

        
        for(let i = 1; i <= timeDetail.length; i++){
            const item = timeDetail[i-1];
            submitFormData.append("1000134_"+-i,he.escape(item["time"]));
            submitFormData.append("1000135_"+-i,he.escape(item["type"]));
            submitFormData.append("1000136_"+-i,he.escape(item["name"]));
            submitFormData.append("1000137_"+-i,he.escape(item["content"]));
        }
    
        fetch("https://ap9.ragic.com/js1031222/tour/2?api&v=3", {
            "method":"post",
            "headers":new Headers({
                "Authorization": "Basic aHdkVEdYblovMzY4aWk0bDlXVEhyakJJOE9NSjU1T0tnaktWYnJ5elpNS2FLTjU2QWNwUGdweFhGalRBVVFxcw=="
            }),
            "body": submitFormData
        }).then(res => res.json())
        .then(data => {
            if(data["status"] === "SUCCESS") {
                setSaving(false);
                removeAddEvent();
                dispatch(fetchMainPage());
            }
        })

        // canvas.toBlob(function(blob){
            // const submitFormData = new FormData();
            // submitFormData.append("1000130", he.escape(formData["name"]));
            // submitFormData.append("1000132", he.escape(formData["tags"]));
            // //submitFormData.append("1000133", resizedDataUrl);
            // submitFormData.append("1000133", blob);
            // submitFormData.append("1000140", 0);
            // submitFormData.append("1000141", 0);
    
            
            // for(let i = 1; i <= timeDetail.length; i++){
            //     const item = timeDetail[i-1];
            //     submitFormData.append("1000134_"+-i,he.escape(item["time"]));
            //     submitFormData.append("1000135_"+-i,he.escape(item["type"]));
            //     submitFormData.append("1000136_"+-i,he.escape(item["name"]));
            //     submitFormData.append("1000137_"+-i,he.escape(item["content"]));
            // }
        
            // fetch("https://ap9.ragic.com/js1031222/tour/2?api&v=3", {
            //     "method":"post",
            //     "headers":new Headers({
            //         "Authorization": "Basic aHdkVEdYblovMzY4aWk0bDlXVEhyakJJOE9NSjU1T0tnaktWYnJ5elpNS2FLTjU2QWNwUGdweFhGalRBVVFxcw=="
            //     }),
            //     "body": submitFormData
            // }).then(res => res.json())
            // .then(data => {
            //     if(data["status"] === "SUCCESS") {
            //         setSaving(false);
            //         removeAddEvent();
            //         dispatch(fetchMainPage());
            //     }
            // })

        // },'image/webp', 0.1)
        //var resizedDataUrl = canvas.toDataURL('image/jpeg');
    
    }, 500);
  }
  
  const testing = ()=>{
    let img = document.querySelector("#insertImg");
    var canvas = document.createElement('canvas');
    canvas.width = 900;
    canvas.height = 600;
    var ctx = canvas.getContext('2d');
    ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
    var resizedDataUrl = canvas.toDataURL('image/jpeg');

    const submitFormData = new FormData();
    submitFormData.append("1000514", resizedDataUrl);

    fetch("http://localhost/jjjjj/test2/2/154?api&v=3", {
        "method":"post",
        "headers":new Headers({
            "Authorization": "Basic aHdkVEdYblovMzY4aWk0bDlXVEhycDZENHROcStxdTYrZFRaelpnNU1GbUVaV3hOckZuT3JVOUtCL3ppbVdsSQ=="
        }),
        "body": submitFormData
    }).then(res => res.json())
    .then(data => {
        console.log(data);
    })

  }
  return (  
    <>
        <div className='addEventBlock'  style={steps === 2? {width:"800px"}:{}} >
            <div className='detailTitle'>
                <div className='cancelAddNewEvent' onClick={removeAddEvent}>取消</div>
                <div onClick={testing}>新增行程</div>
                {steps === 2? <div className='saveAddNewEvent' onClick={saveAddEvent}>
                               {saving? <div className='saveLoading'></div>:""}
                                儲存
                              </div>
                    :""
                } 
            </div>
            <label id="fileInput2" htmlFor="fileInput">從電腦選擇</label>
            <input accept='.jpg,.png,.webp' onChange={uploadImg} id="fileInput" type="file" style={{display: "none"}}></input>

            {steps === 1? 
                <div className='addEventBlockContainer'>
                        <div className='imgUploadContainer'>
                            <FontAwesomeIcon icon={faImage} className="navIcons" />
                            <h3>來上傳一張行程照片吧!!</h3>
                        </div>
                </div>
                :null
            }
            {steps === 2? 
               <div className='addEventDetailContainer'>
                    <div className='savingBackground' style={saving?{display:"block"}:{display:"none"}}></div>
                    <div className='imgUploadContainer'>
                        <div className="spinner"></div>
                        {previewUrl? <img id='insertImg' src={previewUrl} alt='Tour Photo'></img>:""}
                    </div>
                    <div className='addEventDetail'>
                        <div className='insertTitleContainer'>
                            <input name='name' onChange={handleInputChange} value={formData.name} type='text' placeholder='活動名稱'></input>
                        </div>
                        <div className='insertTagContainer'>
                            <input ref={tagRef} type='text' placeholder='新增標籤 : #台北 #餐廳' name='tags' onChange={handleInputChange} value={formData.tags}></input>
                            <div className="autocompleteList" style={tags.length === 0? {display:"none"}:{display:"block"}}>
                                <div className='autocompleteListContainer'>
                                    {tags.map(item=>{
                                        if(item){
                                            return <div>{item["tag"] + "   -   " +item["count"] +"則"}</div>
                                        }
                                    })}
                                </div>
                            </div>
                        </div>
                        <p>行程</p>
                        {timeDetail.map((item,index)=>{
                            return <AddTimeDetail data={item} index={index}/>
                        })}
                        <div className='addNewTourIconContainer'>
                            <div className="addNewTourContent" onClick={()=>dispatch(addNewTimeDetail())}>
                                <FontAwesomeIcon icon={faPlus} className="navIcons" />
                            </div>
                            <div className="addNewTourContent" onClick={removeNewTimeDetail} style={timeDetail.length === 1? {cursor:"not-allowed"}:{}}>
                                <FontAwesomeIcon icon={faTrashCan} className="navIcons" />
                            </div>
                        </div>
                    </div>
                </div>:null
            }
        </div>
        <div onClick={removeAddEvent} className='background'></div>
        {saveWarning? <Warning setSaveWarning={setSaveWarning} saveWarning={saveWarning} />:null}
    </>
  )
}
