import React from 'react'
import "../Style/Warning.scss"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTriangleExclamation } from '@fortawesome/free-solid-svg-icons'

export default function Warning({saveWarning, setSaveWarning}) {
  return (
    <div >
      <div className='warningBackground' onClick={()=>setSaveWarning("")}></div>
      <div className='warningFloatWin'>
        <div>
            <FontAwesomeIcon icon={faTriangleExclamation} className="navIcons" />
            {saveWarning}
        </div>
        <div onClick={()=>setSaveWarning("")} className='confirmWarning'>確定</div>
      </div>
    </div>
  )
}
